// components/event-card.jsx
'use client'

import React from "react"
import Image from "next/image"
import { Calendar, MapPin } from "lucide-react"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardFooter, CardHeader } from "@/components/ui/card"
import { Badge } from "@/components/ui/badge"

export function EventCard({ event, isAdmin, onAction }) {
  // Extract properties from event object with fallbacks
  const { 
    id, 
    name, 
    description, 
    startDate, 
    endDate, 
    location, 
    category, 
    image, 
    __typename, 
    isLikedByUser, 
    isSavedByUser, 
    creatorType 
  } = event || {};

  // Format date for display
  const formattedDate = startDate ? new Date(startDate).toLocaleDateString() : "TBD";
  const formattedEndDate = endDate ? new Date(endDate).toLocaleDateString() : null;

  return (
    <Card className="overflow-hidden">
      <CardHeader className="p-0">
        <div className="relative h-48 w-full">
          {/* Updated placeholder that won't cause 404 errors */}
          <Image
            src={image || "/images/placeholder.png"}
            alt={name || "Event"}
            fill
            className="object-cover"
          />
          
          {__typename === 'ExternalEvent' && (
            <Badge className="absolute top-2 right-2 bg-blue-500">
              External
            </Badge>
          )}
          
          {isAdmin && creatorType && ['UNIVERSITY', 'COMPANY'].includes(creatorType) && (
            <Badge className="absolute top-2 left-2 bg-yellow-500">
              Needs Approval
            </Badge>
          )}
        </div>
      </CardHeader>
      <CardContent className="p-4">
        <div className="mb-2">
          <Badge variant="outline">{category || "Event"}</Badge>
        </div>
        <h3 className="text-xl font-semibold mb-2">{name || "Unnamed Event"}</h3>
        <p className="text-sm text-muted-foreground mb-3 line-clamp-2">
          {description || "No description available"}
        </p>
        <div className="flex items-center gap-2 text-sm text-muted-foreground mb-2">
          <Calendar className="h-4 w-4" />
          <span>{formattedDate}</span>
          {formattedEndDate && formattedDate !== formattedEndDate && (
            <span>- {formattedEndDate}</span>
          )}
        </div>
        <div className="flex items-center gap-2 text-sm text-muted-foreground">
          <MapPin className="h-4 w-4" />
          <span>{location || "Location TBD"}</span>
        </div>
      </CardContent>
      <CardFooter className="p-4 pt-0 flex justify-between">
        <Button variant="outline" size="sm">View Details</Button>
        <Button 
          variant="default" 
          size="sm" 
          onClick={() => onAction && onAction('rsvp')}
        >
          RSVP
        </Button>
      </CardFooter>
    </Card>
  )
}

export default EventCard